          title: 'Using Intelligent Information Systems in AI',
          localeCode: 'en-US',
          description:
            '<p>The world of technology continues to transform at a rapid pace, with intelligent technology incorporated at every stage of the business process. Intelligent information systems (IIS) reduce the need for routine human labor and allow companies to focus instead on hiring creative professionals.</p> <p>In this course, you&#39;ll explore the present and future roles of intelligent informational systems in AI development, recognizing the current demand for IIS specialists. You&#39;ll list several possible IIS applications and learn about the roles AI and ML play in creating them.</p> <p>Next, you&#39;ll identify significant components of IIS and the purpose of these components. You&#39;ll examine how you would go about creating a self-driving vehicle using IIS components. Finally, you&#39;ll work with Python libraries to build high-level components of a Markov decision process.</p>',
