          title: 'API Development: Cloud API Management',
          localeCode: 'en-US',
          description:
            '<p>Because of the magnitude of microservice-based API products available online, the management and protection of API access is a critical and vital security concern. Being familiar with how to use a single service for all of the tasks involved will make this ongoing responsibility easier. In this course, you&#39;ll learn how to use the Azure API Management service to deploy and host API web services in the cloud.</p> <p>You&#39;ll begin by importing and publishing APIs and creating and publishing API products, before creating mock API responses, and transforming and protecting APIs, all using Azure API Management. Next, you&#39;ll use this same service to monitor published APIs, debug APIs, create non-breaking revision changes, and publish multiple API versions. Lastly, you&#39;ll learn how to use the Azure API Management developer portal.</p>',
