          localeCode: 'en-US',
          description:
            '<p>When valuable top performers choose to terminate their contracts to take up more challenging positions elsewhere, it can be a consequence of poor performance management. Identifying a performance problem early and diagnosing it accurately is key to managing performance effectively. You&#39;ve got to involve employees in what might be difficult conversations to discover actual root causes and come up with the best possible solutions.</p> <p>In this course, you&#39;ll learn how to detect, identify, and question problems in your workplace, determine the scope, frequency, and impact when they occur, and diagnose root causes  both external and internal  to help find the best solution and avoid a contract termination of a valuable employee.</p>',
