          description:
            '<p>Do you know if your team&#39;s goals are consistently being met or if employees are always performing at their peak? Do you dread difficult conversations when there&#39;s a performance problem that needs to be addressed? Effectively managing performance can help avoid termination of employees and boost productivity at all levels of your organization. Challenging and engaging top performers is key, as these high achievers are very valuable to the company and may terminate their contracts to seek challenges elsewhere.</p> <p>In this course, you&#39;ll learn about the factors involved in successful performance management. You&#39;ll explore the phases of the performance management process, critical success factors, and key performance indicators. Finally, you&#39;ll learn about identifying key job requirements when creating a role profile.</p>',
